import { defineComponent, PropType, ref } from 'vue';

import BasicTopBar from '@/pages/layout/BasicTopBar';
import DeckerLayout from '@/pages/layout/DeckerLayout';
import Button from '../Button/Button';
import SvgIcon from '../svgIcon/SvgIcon';
import Barcode, { CameraTypes } from './Barcode';

export default defineComponent({
  name: 'BarcodeDecker',
  props: {
    onBarcodeDetect: {
      type: Function as PropType<(result: string) => Promise<boolean>>,
    }
  },
  setup(props) {
    const camera = ref<CameraTypes>('rear');

    return () => <DeckerLayout fullWidth
      style={{
        backgroundColor: '#000'
      }}
      topBar={<BasicTopBar
        leftSlots={[]}
        rightSlots={[<Button
          circle
          onClick={() => {
            camera.value = camera.value === 'rear' ? 'front' : 'rear';
          }}
        >
          <SvgIcon name="cameraSwitch" />
        </Button>]}
      />}
    >
      <Barcode cameraType={camera.value} onBarcodeDetect={props.onBarcodeDetect} />
    </DeckerLayout>;
  }
});

