import { computed, defineComponent, PropType, ref, toRefs, watch } from 'vue';
import cn from 'classnames';

import { useVirtualizer } from '@tanstack/vue-virtual';
import { useI18n } from 'vue-i18n';

import SvgIcon from '@/components/svgIcon/SvgIcon';

import { BookInCellRawCover, getBookCover } from '../book/utils';
import db from '@/services/db';

import { NPopconfirm, NRate } from 'naive-ui';

import styles from './bookList.module.less';

export default defineComponent({
  name: 'BookList',
  props: {
    data: {
      type: Array as PropType<BookInCellRawCover[]>,
      default: () => ([])
    },
    onPreviewBook: {
      type: Function as PropType<(book: BookInCellRawCover) => void>,
    },
    onEditBook: {
      type: Function as PropType<(book: BookInCellRawCover) => void>,
    },
    onDeleteBook: {
      type: Function as PropType<(book: BookInCellRawCover) => void>,
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    const { t } = useI18n({ useScope: 'global' });

    const coversSrc = ref<string[]>([]);
    watch(() => data.value, (newVal) => {
      db.book.queryCoversById(newVal.map(v => v.covers[0] || '')).then(res => {
        coversSrc.value = res.map((cover, index) => getBookCover(cover?.file, newVal[index].title));
      });
    }, { immediate: true });

    const holdingTarget = ref<number>();
    const holdingTouch = ref(false);
    const timer = ref<number>();
    const resetTouch = () => {
      holdingTarget.value = undefined;
      holdingTouch.value = false;
      window.clearTimeout(timer.value as number);
      timer.value = undefined;
      popShow.value = false;
      popPosition.value = [-999, -999];
    };

    const onTouchstart = (e: TouchEvent) => {
      // ÈùûÂçïÊåáÊìç‰ΩúË∑≥Ëøá
      if (e.touches.length !== 1) {
        return;
      }
      // ÁÇπÂáªÂ∑•ÂÖ∑Ê†è‰∏çÈúÄË¶ÅÂèòÊõ¥Áä∂ÊÄÅ
      const target = (e.target as HTMLDivElement)?.dataset?.target;
      if (target === 'tools') {
        return;
      }
      const indexStr = (e.target as HTMLDivElement)?.dataset?.index || '';
      const index = Number.parseInt(indexStr, 10);
      const needReset =
        // Áä∂ÊÄÅÂºÇÂ∏∏ÔºåÈáçÁΩÆÁä∂ÊÄÅ
        holdingTouch.value ||
        // ÂèñÊ∂àÈÄâ‰∏≠ÊÄÅ
        holdingTarget.value !== undefined;
      if (needReset) {
        resetTouch();
      } else {
        if (!Number.isNaN(index)) {
          holdingTouch.value = true;
          timer.value = window.setTimeout(() => {
            if (holdingTouch.value) {
              holdingTouch.value = false;
              holdingTarget.value = index;
            } else {
              onTouchmove();
            }
          }, 5e2);
        }
      }
    };

    const onTouchmove = () => {
      if (holdingTarget.value === undefined && holdingTouch.value) {
        resetTouch();
      }
    };

    const onTouchend = () => {
      onTouchmove();
    };

    const onTouchcancel = () => {
      onTouchmove();
    };

    const popPosition = ref<[number, number]>([-999, -999]);
    const popShow = ref(false);
    const tools = [
      {
        name: 'preview',
        icon: 'üëÄÔ∏è',
        cb: () => {
          if (holdingTarget.value !== undefined) {
            const book = data.value[holdingTarget.value];
            props.onPreviewBook?.(book);
          }
          resetTouch();
        }
      },
      {
        name: 'edit',
        icon: <SvgIcon name="pencil" />,
        cb: () => {
          if (holdingTarget.value !== undefined) {
            const book = data.value[holdingTarget.value];
            props.onEditBook?.(book);
          }
          resetTouch();
        },
      },
      {
        name: 'delete',
        icon: <SvgIcon name="delete" />,
        cb: (e: MouseEvent) => {
          const { x, y } = e;
          popPosition.value = [x, y];
          popShow.value = true;
        },
      }
    ];
    const onClick = (e: MouseEvent) => {
      const toolName = (e.target as HTMLDivElement)?.dataset?.tool;
      const tool = tools.find(t => t.name === toolName);
      if (tool) {
        tool.cb(e);
      }
    };

    const rootRef = ref<HTMLElement | null>(null);
    const rowVirtualizerOptions = computed(() => {
      return {
        count: data.value.length,
        getScrollElement: () => rootRef.value,
        estimateSize: () => 120,
        overscan: 3,
        gap: 16
      };
    });
    const rowVirtualizer = useVirtualizer(rowVirtualizerOptions);
    const virtualRows = computed(() => rowVirtualizer.value.getVirtualItems());
    const totalSize = computed(() => rowVirtualizer.value.getTotalSize());

    return () => (<>
      <div
        class={cn(styles['book-list'])}
        ref={rootRef}
        onTouchstart={onTouchstart}
        onTouchmove={onTouchmove}
        onTouchend={onTouchend}
        onTouchcancel={onTouchcancel}
        onClick={onClick}
      >
        <div
          class={cn(styles['virtual-list'])}
          style={{
            '--list-height': `${totalSize.value}px`
          }}
        >
          {virtualRows.value.map((virtualRow) => {
            const { index, key, size, start } = virtualRow;
            const book = data.value[index];
            return (
              <div
                class={cn(styles.book)}
                key={String(key)}
                style={{
                  '--book-height': `${size}px`,
                  '--book-start': `${start}px`,
                }}
              >
                <div
                  class={cn(styles.mask, {
                    [styles.picked]: holdingTarget.value === index
                  })}
                  data-index={index}
                >
                  <div class={cn(styles.tools)}>
                    {tools.map(tool => (<div
                      class={cn(styles.tool)}
                      data-target="tools"
                      data-tool={tool.name}
                    >
                      {tool.icon}
                    </div>))}
                  </div>
                </div>
                <div class={cn(styles.info)}>
                  <div
                    class={cn(styles.cover)}
                    style={{ backgroundImage: `url(${coversSrc.value[index]})` }}
                  />
                  <div class={cn(styles.abstract)}>
                    <div class={cn(styles['full-text'], styles.title)}>
                      {book.title}
                    </div>
                    <div class={cn(styles['full-text'])}>
                      {book.authors ? book.authors?.join(' / ') : t('pages.cell.bookList.unknownAuthor')}
                    </div>
                    <div class={cn(styles['full-text'])}>
                      {book.publishers ? book.publishers?.join(' / ') : t('pages.cell.bookList.unknownPublisher')}
                    </div>
                    <div>isbn: {book.isbn}</div>
                    <NRate
                      count={5}
                      value={book.rating}
                      readonly
                      size="small"
                    />
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
      {/* Âà†Èô§Á°ÆËÆ§Ê∞îÊ≥° */}
      <NPopconfirm
        showIcon={false}
        placement="left-end"
        trigger="manual"
        positiveText={t('common.delete')}
        negativeText={t('common.forgetIt')}
        positive-button-props={{
          type: 'error',
        }}
        onPositiveClick={() => {
          if (holdingTarget.value !== undefined) {
            const book = data.value[holdingTarget.value];
            props.onDeleteBook?.(book);
          }
          resetTouch();
        }}
        onNegativeClick={() => {
          popShow.value = false;
        }}
        onClickoutside={() => {
          popShow.value = false;
        }}
        x={popPosition.value?.[0]}
        y={popPosition.value?.[1]}
        show={popShow.value}
      >
        {t('common.deleteConfirm')}
      </NPopconfirm>
    </>);
  }
});
