import { defineComponent, onMounted, onUnmounted, PropType, ref, watch } from 'vue';
import cn from 'classnames';

import BcrWorker from './barcodeRecognize.worker?worker';
import { BarcodeResult } from './barcodeRecognize.worker';

import styles from './barcode.module.less';

/** ÊëÑÂÉèÂ§¥Á±ªÂûã ÂâçÁΩÆ / ÂêéÁΩÆ */
export type CameraTypes = 'front' | 'rear'

const cameras: {
  [key in CameraTypes]: MediaStreamConstraints['video']
} = {
  front: { facingMode: 'user' },
  rear: { facingMode: 'environment' }
};

export default defineComponent({
  name: 'Barcode',
  props: {
    cameraType: {
      type: String as PropType<CameraTypes>,
      default: 'rear',
    },
    onDevicesDetect: {
      type: Function as PropType<(devices: MediaDeviceInfo[]) => void>,
    },
    onBarcodeDetect: {
      type: Function as PropType<(result: string) => Promise<boolean>>,
    }
  },
  setup(props) {
    const videoRef = ref<HTMLVideoElement>();
    const streamRef = ref<MediaStream>();

    const pause = ref(false);

    const initDevices = () => {
      return navigator.mediaDevices.enumerateDevices().then(devices => {
        console.log(devices);
        const videoInputDevices = devices.filter(device => device.kind === 'videoinput');
        props.onDevicesDetect?.(videoInputDevices);
        return videoInputDevices;
      });
    };

    const initVideo = () => {
      navigator.mediaDevices.getUserMedia({ video: cameras[props.cameraType], audio: false })
        .then((stream) => {
          if (!videoRef.value) {
            return Promise.reject();
          }
          streamRef.value = stream;
          videoRef.value.srcObject = stream;
          videoRef.value.play();
          videoRef.value.onloadedmetadata = () => {
            capture();
          };
        })
        .catch((err) => {
          console.error('Error accessing the camera: ' + err);
        });
    };

    const clearVideo = () => {
      if (streamRef.value) {
        streamRef.value.getTracks().forEach((track) => {
          track.stop();
        });
        streamRef.value = undefined;
      }
      if (videoRef.value) {
        videoRef.value.pause();
        videoRef.value.srcObject = null;
      }
      window.clearTimeout(timer.value);
      timer.value = undefined;
    };

    const workerRef = ref<null | InstanceType<typeof BcrWorker>>(null);
    const capture = () => {
      if (!videoRef.value) {
        return Promise.reject();
      }
      const width = videoRef.value.videoWidth;
      const height = videoRef.value.videoHeight;

      const canvas = new OffscreenCanvas(640, 640);
      canvas.width = width;
      canvas.height = height;
      const ctx = canvas?.getContext('2d');
      ctx?.drawImage(videoRef.value, 0, 0);
      // canvas.convertToBlob().then(res => {
      //   // console.log(res);
      //   const url = URL.createObjectURL(res)
      //   // console.log(url);
      //   setTimeout(() => {
      //     capture()
      //   }, 5e3)
      // })
      const isReady =
        width > 0 &&
        height > 0 &&
        ctx &&
        !pause.value;
      if (isReady && workerRef.value) {
        const imageData = ctx.getImageData(0, 0, width, height);

        workerRef.value.postMessage(imageData, [imageData.data.buffer]);

        workerRef.value.onmessage = (e: MessageEvent<BarcodeResult>) => {
          const { output, corners, result } = e.data;

          // TODO ËøòÂèØ‰ª•ÁªßÁª≠‰ºòÂåñ
          if (result) {
            barcodeResult.value = result;
            if (!pause.value) {
              pause.value = true;
              props.onBarcodeDetect?.(result).then(res => {
                pause.value = res;
              });
            }
          }

          // let imageData = new ImageData(new Uint8ClampedArray(output), width, height);

          const cc = canvasRef.value;
          // const video = videoRef.value;
          // video.width = width
          // video.height = height
          if (cc) {
            cc.width = width;
            cc.height = height;
          }
          const cctx = cc?.getContext('2d');
          if (cctx) {
            // cctx?.putImageData(imageData, 0, 0)

            cctx.strokeStyle = 'red';
            cctx.lineWidth = 2;
            cctx.beginPath();
            cctx.moveTo(corners[0], corners[1]);
            for (let i = 2; i < corners.length; i += 2) {
              cctx.lineTo(corners[i], corners[i + 1]);
            }
            cctx.closePath();
            cctx.stroke();
          }
          capture();
        };
      }
    };

    const timer = ref<number>();
    watch(() => pause.value, (val) => {
      if (!val) {
        // Â¢ûÂä†Âª∂ËøüÈÅøÂÖçÈ¨ºÁïú
        timer.value = window.setTimeout(() => {
          capture();
        }, 1e3);
      }
    });

    watch(() => props.cameraType, () => {
      clearVideo();
      initVideo();
    });

    const barcodeResult = ref<string>('');
    const canvasRef = ref<HTMLCanvasElement>();

    onMounted(() => {
      initDevices().then(() => {
        initVideo();
      });
      workerRef.value = new BcrWorker();
    });

    onUnmounted(() => {
      // ËµÑÊ∫êÂõûÊî∂
      workerRef.value?.terminate();
      workerRef.value = null;
      clearVideo();
    });

    return () => (<>
      <div class={cn(styles.barcode)}>
        <video ref={videoRef} playsinline />
        <canvas ref={canvasRef} />
      </div>
    </>);
  }
});

